import { pluginName } from './constants';

const CKEDITOR = window.CKEDITOR;

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colors = CKEDITOR.tools.get_plugin_config(pluginName, editor);

    /* ------------------------------------------------- */
    /* Helper – replicate ancestor chain INSIDE a node   */
    /* ------------------------------------------------- */
    function wrapInside(node, chain) {
      const wrapped = chain.reduceRight((inner, outer) => {
        const clone = new CKEDITOR.dom.element(outer.getName());
        Object.entries(outer.getAttributes()).forEach(([k, v]) => clone.setAttribute(k, v));
        clone.append(inner);
        return clone;
      }, node);
      console.log('🔧 wrapInside →', wrapped.getOuterHtml());
      return wrapped;
    }

    /* ------------------------------------------------- */
    /* Lift colour spans so span wraps <strong>/<em>/…   */
    /* ------------------------------------------------- */
    /* ------------------------------------------------- */
    /* Lift colour spans **in‑place** so inside parent   */
    /* <strong><span>txt</span></strong>  →  <strong><span><strong>txt</strong></span></strong> */
    /* ------------------------------------------------- */
    function liftColorSpans(range) {
      const r = range.clone();
      r.enlarge(CKEDITOR.ENLARGE_INLINE);
      const walker = new CKEDITOR.dom.walker(r);
      walker.evaluator = n => n && n.type === CKEDITOR.NODE_ELEMENT && n.getName() === 'span' && n.getStyle('color');

      let span;
      while ((span = walker.next())) {
        const parent = span.getParent();
        if (!parent || parent.getName() === 'span') continue; // nothing to fix
        if (span.getFirst() && span.getFirst().getName() === parent.getName()) continue; // already fixed

        console.log('🚀 In‑place lift', span.getOuterHtml(), 'inside', parent.getName());

        // 1️⃣ Clone the inline parent (<strong>/<em>/…>) without children
        const clone = new CKEDITOR.dom.element(parent.getName());
        Object.entries(parent.getAttributes()).forEach(([k, v]) => clone.setAttribute(k, v));

        // 2️⃣ Move ALL current children of <span> into the clone
        while (span.getFirst()) clone.append(span.getFirst().remove());

        // 3️⃣ Append the clone back into the <span>
        span.append(clone);

        console.log('✅ After in‑place lift →', parent.getOuterHtml());
      }
    }

    /* ------------------------------------------------- */
    /* Remove colour only, keep other inline styles      */
    /* ------------------------------------------------- */
    function removeColor(range) {
      const walker = new CKEDITOR.dom.walker(range.clone());
      walker.evaluator = n => n.type === CKEDITOR.NODE_TEXT && n.getAscendant('span', true)?.getStyle('color');

      let text;
      while ((text = walker.next())) {
        const colourSpan = text.getAscendant('span', true);
        if (!colourSpan) continue;

        const start = text.equals(range.startContainer) ? range.startOffset : 0;
        const end = text.equals(range.endContainer) ? range.endOffset : text.getLength();
        const parts = [text.substring(0, start), text.substring(start, end), text.substring(end)];

        const chain = [];
        let n = text.getParent();
        while (n && !n.equals(colourSpan)) { chain.unshift(n); n = n.getParent(); }

        const make = (content, keepColor) => {
          if (!content) return null;
          const inside = wrapInside(new CKEDITOR.dom.text(content), chain);
          if (!keepColor) return inside;
          const s = new CKEDITOR.dom.element('span');
          s.setAttribute('style', colourSpan.getAttribute('style'));
          s.append(inside);
          return s;
        };

        [make(parts[0], true), wrapInside(new CKEDITOR.dom.text(parts[1]), chain), make(parts[2], true)]
          .filter(Boolean)
          .forEach(frag => colourSpan.insertBeforeMe(frag));

        text.remove();
        if (!colourSpan.getChildCount()) colourSpan.remove();
      }
    }

    /* ------------------------------------------------- */
    /* RichCombo UI                                     */
    /* ------------------------------------------------- */
    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        Object.entries(colors).forEach(([label, val]) => this.add(val, label, label));
      },
      onRender() { this.setValue(''); },
      onClick(value) {
        editor.focus();
        const sel = editor.getSelection();
        if (!sel) return;
        const ranges = sel.getRanges();
        editor.fire('lockSnapshot');

        if (value === 'default') {
          console.log('🗑 Removing colour from', ranges.length, 'range(s)');
          ranges.forEach(removeColor);
        } else {
          console.log('🎨 Applying colour', value, 'to', ranges.length, 'range(s)');
          const style = new CKEDITOR.style({ element: 'span', styles: { color: value } });
          editor.applyStyle(style);
          ranges.forEach(liftColorSpans);
        }

        editor.fire('unlockSnapshot');
      }
    });
  }
});
